import React from 'react';
import PropTypes from 'prop-types';
import {Panel, Group, InfoRow, List, Cell, Footer} from '@vkontakte/vkui';

const Prizes = props => (
  <Panel id={props.id}>
    {props.panelHeader}
    <Group>
      <List>
        <Cell>
          <InfoRow title=" ">
             iPhone XS Max, 512 ,   
          </InfoRow>
        </Cell>
        <Cell>
          <InfoRow title=" ">
            - GoPro Hero 7 Black + -   DJI Osmo Mobile 2
          </InfoRow>
        </Cell>
        <Cell>
          <InfoRow title=" ">
            - GoPro Hero 7 Black
          </InfoRow>
        </Cell>
      </List>
    </Group>
    <Footer>3 </Footer>
  </Panel>
);

Prizes.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
  panelHeader: PropTypes.object.isRequired,
};

export default Prizes;



